import React, { Component } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    Redirect,
    useHistory,
    useLocation
  } from "react-router-dom";

class Contact extends Component {
    constructor(props){
        super(props);
        this.state={
            isRedirect: false,
        }
    }

    submitForm = (event)=>{
        event.preventDefault(); // ngan khong cho chuyen trang
        this.setState({
            isRedirect: true
        })
    }

    isChange = (event)=>{
        const ten = event.target.name;
        const giatri = event.target.value;
        this.setState({
            [ten]: giatri
        })
    }
    render() {
        if(this.state.isRedirect===true){
            console.log(this.state.fgioitinh);
            return <Redirect to="/"/>;
        }
        return (
            <div>
                <section className="page-section" id="contact">
                    <div className="container">
                    {/* Contact Section Heading */}
                    <h2 className="page-section-heading text-center text-uppercase text-secondary mb-0">Contact Me</h2>
                    {/* Icon Divider */}
                    <div className="divider-custom">
                        <div className="divider-custom-line" />
                        <div className="divider-custom-icon">
                        <i className="fas fa-star" />
                        </div>
                        <div className="divider-custom-line" />
                    </div>
                    {/* Contact Section Form */}
                    <div className="row">
                        <div className="col-lg-8 mx-auto">
                        {/* To configure the contact form email address, go to mail/contact_me.php and update the email address in the PHP file on line 19. */}
                        <form name="sentMessage" id="contactForm" noValidate="novalidate">
                            <div className="control-group">
                            <div className="form-group floating-label-form-group controls mb-0 pb-2">
                                <label>Name</label>
                                <input name="fname" onChange={(event)=>{this.isChange(event)}} className="form-control" id="name" type="text" placeholder="Name" required="required" data-validation-required-message="Please enter your name." />
                                <p className="help-block text-danger" />
                            </div>
                            </div>
                            <div className="control-group">
                            <div className="form-group floating-label-form-group controls mb-0 pb-2">
                                <label>Email Address</label>
                                <input name="femail" className="form-control" id="email" type="email" placeholder="Email Address" required="required" data-validation-required-message="Please enter your email address." />
                                <p className="help-block text-danger" />
                            </div>
                            </div>
                            <div className="control-group">
                            <div className="form-group floating-label-form-group controls mb-0 pb-2">
                                <label>Phone Number</label>
                                <input name="fphone" className="form-control" id="phone" type="tel" placeholder="Phone Number" required="required" data-validation-required-message="Please enter your phone number." />
                                <p className="help-block text-danger" />
                            </div>
                            </div>
                            <div className="control-group">
                            <div className="form-group floating-label-form-group controls mb-0 pb-2">
                                <label>Message</label>
                                <textarea name="fmess" className="form-control" id="message" rows={5} placeholder="Message" required="required" data-validation-required-message="Please enter a message." defaultValue={""} />
                                <p className="help-block text-danger" />
                            </div>
                            </div>

                            <div className="control-group">
                            <div className="form-group floating-label-form-group controls mb-0 pb-2">
                                 <select className="form-control" name="fgioitinh" id="" onChange={(event)=>{this.isChange(event)}}>
                                   <option value="Male">Male</option>
                                   <option value="Female">Female</option>
                                   <option value="Other">Other...</option>
                                 </select>
                               </div>
                                <p className="help-block text-danger" />
                            </div>
                            <br />
                            <div id="success" />
                            <div className="form-group">
                            <button type="submit" onClick={(event)=>{this.submitForm(event)}} className="btn btn-primary btn-xl" id="sendMessageButton">Send</button>
                            </div>
                        </form>
                        </div>
                    </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default Contact;