import React, {Component} from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";


export default class Topmenu extends Component{

	render(){
		return(

			 <nav className="navbar navbar-expand-lg navbar-light fixed-top py-3" id="mainNav">
		        <div className="container">
		          <a className="navbar-brand js-scroll-trigger" href="#page-top">Tin Tức</a>
		          <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
		            <span className="navbar-toggler-icon" />
		          </button>
		          <div className="collapse navbar-collapse" id="navbarResponsive">
		            <ul className="navbar-nav ml-auto my-2 my-lg-0">

					  {/* them li cua react router */}
					  <li className="nav-item">
						<Link className="nav-link js-scroll-trigger aaa"  to="/">Home</Link>
						</li>
					  <li className="nav-item">
						<Link className="nav-link js-scroll-trigger"  to="/news">News</Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link js-scroll-trigger" to="/newsdetail/:slug.:id.html">NewsDetail</Link>
						</li>
						<li className="nav-item">
							<Link className="nav-link js-scroll-trigger" to="/contact">Contact</Link>
						</li>
					</ul>
		          </div>
		        </div>
		      </nav>
			);
	}
}